body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  border: none;
  box-sizing: border-box;
  text-decoration: none;
  color: rgb(0, 0, 0);
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", Monaco;
}

body {
  background-color: rgb(231, 231, 231);
  text-decoration: none;
}

.guled-pic {
  display: block;
  height: 300px;
  width: 400px;
  justify-content: center;
  margin: auto;
  border-radius: 20px;
  margin-top: 30px;
}

.guled-name {
  text-align: center;
  height: 180px;
  background-image: url("../src/assets/nyc.jpg");
  background-position: 70px;
}
.guled-name h1 {
  color: white;
  font-size: 3rem;
  height: 20px;
  padding: 50px;
}

.about-me {
  text-align: center;
  margin: 0 auto;
}

.about-me p {
  padding: 30px;
  border: 2px solid black;
  border-radius: 50px;
  margin: 40px;
  max-width: 1000px;
  margin: 0 auto 40px;
  margin-top: 50px;
}


.flex {
  display: flex;
  flex-wrap: wrap;
  text-decoration: none;
  border: 2px solid black;
}

.port-buttons a {
  margin-left: 30px;
  background-color: rgb(29, 29, 29);
  color: white;
  border-radius: 4px;
  font-size: 1.2rem;
  padding: 10px 10px;
}
.port-buttons a:hover {
  background-color: rgb(255, 255, 255);
  color: black;
  margin-top: 20px;
}

p {
  font-size: 1.3rem;
  margin: 20px;
}

ul {
  list-style-type: none;
  justify-content: space-around;
  padding: 25px 40px;
  font-size: 1.5rem;
}

h1 {
  font-size: 2rem;
  padding: 20px 20px;
}

h2 {
  padding: 30px;
}

h3 {
  margin: 20px;
}

input {
  font-size: 2rem;
  border-radius: 4px;
}

form {
  width: 40%;
}

textarea {
  width: 500px;
  height: 100px;
  font-size: 1rem;
}

label {
  font-size: 1.5rem;
  display: block;
}

button {
  border-radius: 4px;
  padding: 20px 30px;
  font-size: 14x;
  background-color: rgb(29, 29, 29);
  color: white;
  margin-top: 20px;
}

button:hover {
  background-color: rgb(255, 255, 255);
  color: black;
  margin-top: 20px;
}

.icon {
  width: 50px;
  margin: 80px;
}

.icon:hover {
  opacity: 0.1;
}

footer {
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.proj {
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.proj-img {
  height: 0%;
  width: 50%;
  border-width: 3px;
  border-style: solid;
  margin: 20px;
  min-width: 300px;
  max-width: 300px;
  width: 250px;
  height: 250px;
  border-radius: 20px;
}

.form-look {
  margin: 40px;
}

.hover a:hover {
  background-color: rgb(29, 29, 29);
  color: white;
  border-radius: 20px;
  padding: 10px 10px;
}



.hover a:active {
  background-color: rgb(29, 29, 29);
  color: white;
  border-radius: 20px;
  padding: 10px 10px;
}
